import React from 'react';
import classes from './ScheduledAppointments.module.css';
import EditIcon from '../../Images/edit.png';
import DeleteIcon from '../../Images/delete.png';



class ScheduledAppointmentList extends React.Component {

    render() {
        const appointments = [
            {
                id: 1,
                date: "June 24, 2020",
                time: "10:30 AM",
                patientName: "Madhu Gupta",
                department: "General Medicine",

            },
            {
                id: 2,
                date: "June 27, 2020",
                time: "09:30 AM",
                patientName: "Madhu Gupta",
                department: "Dermatology",
                doctor: "Mr. Malhotra"
            }


        ]
        const appointmentList = appointments.map(appointment => (
            <ul className={classes.ul}>
                <li>
                    <div className={classes.main}>
                        <div style={{ float: "left", width: "70%" }}> 
                            <label>{appointment.date}</label>
                            <label className={classes.time}>{appointment.time}</label>
                        </div>
                        <div style={{ float: "right", width: "30%" }}>
                            <img className={classes.editIcon} src={EditIcon} />
                            <img className={classes.deleteIcon} src={DeleteIcon} />
                        </div>
                        <br />
                        <label>{appointment.patientName}</label>
                        <br />
                        <label>{appointment.department}</label>
                        <br />
                        <label>{appointment.doctor}</label>
                    </div>

                </li>
            </ul>

        ))


        return (
            <div>
                {appointmentList}
            </div>
        );
    };
};
export default ScheduledAppointmentList;